.doc-outline {
    margin-top: 1.2em;
    margin-bottom: 2em;
    padding: 0.4em 1em 0 1em;
    font-size: 0.9em;
    border-radius: 2px;
    border-width: 1px 0 0 0;
    border-style: solid;
    border-color: #e3e3e3;
}

.theme-night .doc-outline {
    border-color: #2e2e2e
}

.doc-outline ul {
    margin-top: 0.6em;
    margin-bottom: 0.6em;
    list-style-type: none;
}

.doc-outline li {
    margin-left:0;
    margin-bottom:0.8em;
    line-height: 140%;
}

#center-doc-outline {
    margin-top: 0.6em;
    border-width: 1px 0 1px 0;
}

#side-doc-outline
{
    display:none;
}

#social .doc-outline li {
    margin-bottom:0.4em;
}

@media only screen and (min-width: $desktop) {

    #center-doc-outline
    {
        display:none;
    }

    #side-doc-outline
    {
        display:block;
    }

}