#sidebar, #main, #social {
    position: relative;
    float: left;
    width: 100%;
}

/* Right Column */

#social {
    display: block;
    left: 0%;
    right: auto;
    width: 100%;
    padding: 0;
    font-size: 0.9em;
}

@media only screen and (min-width:$desktop){
    #social {
        position: relative;
        float: right;
        padding: 0 1em 0 0;
        width: 20%;
    }
}

/* END Right Column */

/* Left Column */

#sidebar {
    display: none;
    color: #6e6e6e;
    padding:0.01px;/*hack to fix delayed render appearance */
}

@media only screen and (min-width: $tablet) {
    #sidebar {
        display:block;
        width: 20%;
    }
}

@media only screen and (min-width: $desktop) {
    #sidebar {
        width: 20%;
    }
}

/* END Left Column */

/* Center Column */

#main {
    margin-left: 4%;
    margin-right: 4%;
}

@media only screen and (min-width: $tablet) {
    #main {
        width: 76%;
        margin-left: 2%;
        margin-right: 2%;
    }
    
    #comments-container {
        margin-left: 22%;
        width: 76%
    }
}

@media only screen and (min-width: $desktop) {
    #main {
        width:56%;
        margin-left: 2%;
        margin-right: 2%;
    }
    
    #comments-container {
        width: 58%;
    }
}

/* END Center Column */