#sidebar a, #sidebar a:link,v#sidebar a:visited, #sidebar a:active, #sidebar a:hover {
	text-decoration:none;
    color: #6e6e6e;
	cursor:pointer;
}

.theme-night #sidebar a, .theme-night #sidebar a:link, .theme-night #sidebar a:visited, .theme-night #sidebar a:active, .theme-night #sidebar a:hover {
	text-decoration:none;
    color: #6e6e6e;
	cursor:pointer;
}

#sidebar a:hover, #sidebar a:active {
	color: $link;
}

.theme-night #sidebar a:hover, .theme-night #sidebar a:active {
	color: $night-link;
}

#sidebar ol {
    list-style: none;
}

#sidebar li {
    margin: 0;
    padding: 0;
	font-size:14px;
	line-height:16px;
}

#sidebar li.toc-level-0,
#sidebar li.toc-level-1,
#sidebar li.toc-level-2,
#sidebar li.toc-level-3 {
	padding-left:1em;
}

#sidebar .toc-content {
    word-wrap: break-word;
	padding:4px 0px;
	margin:8px 0;
}

#sidebar h2{
    font-size: 15px;
	line-height: 18px;
	font-weight: 600;
	color: #6e6e6e;
	margin: 24px 0 12px 18px;
}

#sidebar span, #sidebar div.strong {
    color: #222;
	font-weight:bold;
}

.theme-night #sidebar span, .theme-night #sidebar div.strong {
    color: $night-text;
}

#sidebar .toc-level-0 div.selected{
    border-left:3px solid #222;
	padding-left:10px;
	margin-left:-13px;
}

.theme-night #sidebar .toc-level-0 div.selected{
    border-color:$night-text;
}

#sidebar .toc-level-2 div.selected{
	padding-left:13px;
	margin-left:-16px;
}

#sidebar .border-left{
	border-left:1px dotted #999;
}

#toc-search{
	margin-top:15px;
}

#toc-filter {
    margin: 5%;
}

#toc-filter-input {
	font-size: 0.875em;
    width:100%;
    padding-left:4px;
}

#toc-search-results{
    display:none;
}